import {
  useEffect, useState, useCallback, useRef, useMemo,
  forwardRef,
  createElement as h
} from 'react'

const ChapterTitle = ({ title, titleStyle }) => {
  return h('h4', {
    style: titleStyle
  }, title)
}


const Paragraph = ({ paragraph, style }) => {
  return h('div', { style }, paragraph.content)
}

const Novel = ({ pages, marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle }) => {
  const [ index, setIndex ] = useState(0)
  const ref = useRef(), prevRef = useRef()
  const animateStatus = useMemo(() => ({ playing: false }), [])

  const
    prev = pages[index - 1],
    next = pages[index + 1],
    page = pages[index]

  const onClick = useCallback(({ clientX }) => {
    if(animateStatus.playing) return
    const newIndex = Math.max(0,
      Math.min(pages.length - 1,
        index + (clientX > 187 ? 1 : -1)
      )
    )
    if(index > newIndex){
      animateStatus.playing = true
      const am = prevRef.current.animate(
        { transform: [ 'skewY(-90deg) scaleX(0)', 'skewY(0deg) scaleX(1)' ] }
      , { duration: 1e3,
        easing: 'ease-out'
      })
      
      am.onfinish = e => {
        animateStatus.playing = false
        setIndex(newIndex)
      }
    }
    else if(index < newIndex){
      animateStatus.playing = true
      const am = ref.current.animate(
        { transform: [ 'skewY(0deg) scaleX(1)', 'skewY(-90deg) scaleX(0)' ] }
      , { duration: 1e3,
        easing: 'ease-in'
      })
      
      am.onfinish = e => {
        animateStatus.playing = false
        setIndex(newIndex)
      }
    }
    else{
      alert('first or last')
    }
  }, [ index ])

  const pageProps = { marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle }
  return h('div', {
    onClick,
    style: {
      position: 'relative',
      width: appStyle.width,
      height: appStyle.height,
      overflow: 'hidden'
    }
  },
    h(Page, { transform: '', page: next, ...pageProps }),
    h(Page, { ref, transform: `scaleX(1) skewY(0deg)`, page, ...pageProps }),
    h(Page, { ref: prevRef, transform: `scaleX(0) skewY(-90deg)`, page: prev, ...pageProps }),
    )
}

const Page = forwardRef(({ transform, page, isPrev, isNext, marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle }, ref) => {
  if(!page) return null

  const style = {
    position: 'absolute',
    top: 0,
    left: 0,
    width: appStyle.width,
    height: viewportHeight,
    overflow: 'hidden',
    willChange: 'transform',
    background: '#ebe1c6',
    transformOrigin: 'left top',
    outline: 'solid 1px #000',
    transform
  }
  return h('div', { ref, style },
    h('div', { style: { transform: `translate(0, ${ page.offset }px)` } },
      page.paragraphs.map(p => p.type === 'title'
        ? h(ChapterTitle, { key: p.data.key, title: chapter.title, titleStyle })
        : h(Paragraph, { key: p.data.key, paragraph: p.data, style: paragraphStyle })
      )
    )
  )
})

const Prerender = ({ chapter, appStyle, titleStyle, paragraphStyle, viewportHeight, setPages }) => {
  const ref = useRef()
  useEffect(() => {
    if(viewportHeight){
      const wrapper = ref.current, titleHeight = wrapper.firstChild.getBoundingClientRect().height
      const pages = [
        {
          paragraphs: [ { type: 'title', data: { key: 'title_' + Math.random(), content: chapter.title } } ],
          height: titleHeight,
          offset: 0
        }
      ]
      const { length } = wrapper.children
      let index = 1, page = pages[0]
      while(index < length){
        const pHeight = wrapper.children[index].getBoundingClientRect().height
        const para = {
          type: 'paragraph',
          data: chapter.paragraphs[index - 1]
        }
        if(page.height + page.offset < viewportHeight){
          page.height += pHeight
          page.paragraphs.push(para)
          
          if(page.height + page.offset > viewportHeight){
            page = {
              paragraphs: [ para ],
              height: pHeight,
              offset: viewportHeight - page.height - page.offset
            }
            pages.push(page)
          }
        }
        else{
          page = {
            paragraphs: [ para ],
            height: pHeight,
            offset: 0
          }
          pages.push(page)
        }
        index ++
      }
      setPages(pages)
    }
  }, [ chapter, viewportHeight ])
  console.log(chapter)
  return h('div', { ref, style: { position: 'absolute', width: appStyle.width, transform: 'translateX(-750px)' } },
    h(ChapterTitle, { title: chapter.title, titleStyle }),
    chapter.paragraphs.map(paragraph => h(Paragraph, { key: paragraph.key, paragraph, style: paragraphStyle }))
  )
}

const appStyle = {
  width: 375,
  height: 800,
  fontSize: 18,
  lineHeight: '32px',
  backgroundColor: '#ccc'
}

const NovelViewer = ({ chapter }) => {
  const [ pages, setPages ] = useState(null)
  const [ marginTop, setMarginTop ] = useState(0)
  const [ viewportHeight, setViewportHeight ] = useState(0)
  const [ titleStyle, setTitleStyle ] = useState(null)
  const [ paragraphStyle, setParagraphStyle ] = useState(null)
  const viewer = useRef()

  useEffect(() => {
    const lineHeight = parseFloat(appStyle.lineHeight)
    const viewportHeight = Math.floor(appStyle.height - lineHeight * 2 - appStyle.height % lineHeight)
    const marginTop = Math.floor((appStyle.height - viewportHeight) * .5)

    setTitleStyle({
      fontSize: appStyle.fontSize * 2,
      margin: 0,
      lineHeight: lineHeight * 2 + 'px',
      padding: `0 ${appStyle.fontSize}px ${ lineHeight }px`
    })
    setParagraphStyle({
      padding: `0 ${appStyle.fontSize}px`,
      boxSizing: 'border-box'
    })
    setMarginTop(marginTop)
    setViewportHeight(viewportHeight)
  }, [ marginTop, viewportHeight ])

  return <div ref={viewer} style={appStyle}>{
    h(Prerender, { chapter, titleStyle, viewportHeight, setPages, appStyle, paragraphStyle })
  }{
    pages ? h(Novel, {
      pages,
      appStyle,
      titleStyle,
      paragraphStyle,
      marginTop,
      viewportHeight
    }) : null
  }</div>
}

export default NovelViewer